.StudentPage {
    nav {
        ul {

            width: fit-content;
            margin-right: auto;
            margin-top: 3%;

            a {
                color: rgba(0, 0, 0, 0.299);

                &:hover {
                    color:black
                }
            }
        }
    }

    form {
        box-shadow: 0px 0px 5px;
        width: 90%;
        height: max-content;
        margin: auto;
        padding: 3%;
        border-radius: 5px;
        background-color: rgba(0, 0, 0, 0.11);
        // margin-left: 5%;
        margin-top: 3%;
        margin-bottom: 3%;
        display: flex;
        justify-content: space-around;

        .leftS {
            width: 50%;
            // margin-left: 8%;
            text-align: left;

            p {
                font-weight: bolder;

                span {
                    font-weight: lighter;
                }
            }
        }

        .rightS {
            // margin-right: 15%;
            width: 30%;

            img {
                width: 55%;
                border-radius: 5px;
                padding: 5px;
                border: 1px solid;
                margin: auto;
            }
        }
    }

    section {
        box-shadow: 0px 0px 5px;
        width: 90%;
        height: max-content;
        margin: auto;
        padding: 3%;
        border-radius: 5px;
        background-color: rgba(0, 0, 0, 0.11);
        margin-top: 3%;
        margin-bottom: 3%;

        img {
            // display: block;
            border-radius: 5px;
            padding: 5px;
            border: 1px solid;
            width: 40%;
            margin-bottom: 2%;
        }


    }
}